div.search-friends-container {
    border-bottom: 1px solid #ddd;
}   

div.search-friends-container > input {
    border-bottom: none;
    margin: 0 !important;
}

div.search-friends-container > input:focus {
    border-bottom: none !important;
    -webkit-box-shadow: none !important;
    box-shadow: none !important;
}

p.online-text {
    color: #4BB543;
}

span.right-text-span {
    font-size: 15px;
}

.collection-title {
    padding: 5px 10px !important;
    text-align: center;
    background-color: rgba(58, 150, 175, 0.6) !important;
}

.collection-title > span {
    font-size: 20px;
}

.collection {
    border: none !important;
}

.collection-item.avatar {
    min-height: unset !important;
}

.chat-section {
    background-color: rgba(58, 150, 175, 0.1) !important;
    height: 100%;
    padding-top: 50px;
}

.chat-container {
    background-color: white;
    width: 80%;
    margin: auto;
}

.friends-col {
    border-right: 1px solid #ddd;
}

.chat-msg-row {
    height: 70vh;
}

.chat-title-row {
    border-bottom: 1px solid #ddd;
}

.message-reverse {
    display: flex;
    flex-direction: row;
    align-items: flex-end;
    justify-content: flex-end;
}

.message {
    display: flex;
    align-items: flex-end;
    justify-content: space-between;
}

.chat-img {
    align-self: center;
}

div.user-receiver {
    display: flex;
    align-items: center;
}

div.message-send {
    text-align: center;
    padding: 8px 12px;
    font-size: 1.2em;
    background-color: rgb(75, 181, 67, 0.4);
    border-radius: 10px; 
}

.message-receive {
    text-align: center;
    padding: 8px 12px;
    font-size: 1.2em;
    background-color: #f6f6f6;
    border-radius: 10px; 
}

.send-btn {
    border-radius: 50%;
    height: 46px !important;
    display: flex;
    margin: 0 !important;
}

.btn-container {
    display: flex;
    justify-content: center;
}

.send-btn > i {
    transform: rotate(-85deg);
}

.chat-box {
    width: 100%;
    border: none;
    height: 98vh;
    overflow: unset;
}