body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.top-space {
  padding-top: 35px !important;
}

nav {
  background-color: white;
  position: relative;
  z-index: 1;
  height: 80px;
}

.full-width {
  width: 100%;
  margin: auto !important;
}

.my-sidenav {
  border-right: 1px solid #ddd;
  height: 150vh;
}

.is-active {
  background-color: #ddd;
}

div.logo-container {
  display: flex;
  align-items: center;
  justify-content: center;
  padding-top: 5px !important;
}

input.search-input {
  border: 1px solid !important;
  border-color: #bbc0c4 !important;
  margin: 0 !important;
  height: 30px !important;
  border-radius: 5px !important;
}

input.search-input:focus {
  border-color: #bbc0c4 !important;
  -webkit-box-shadow: none !important;
  box-shadow: none !important;
}

.btn {
  background-color: #3A96AF !important;
}

.full-height {
  height: 100%;
}

.no-margin {
  margin: 0 !important;
}

.vertical-align {
  display: flex;
  align-items: center;
}

.my-container {
  width: 80%;
  margin: 0 auto;
}

div.titles-container {
  padding-left: 5% !important;
}

h1.main-title {
  font-size: 7.2rem;
}

h3.main-subtitle {
  display: inline-block;
  padding: 5px 10px;
  border: 1px solid transparent;
  border-radius: 30px;
  background-color: rgba(255, 255, 255, 0.7);
}

p.main-p {
  font-size: 1.4em;
}

div.post-card {
  border-bottom: 1px solid #dddddd;
}

h2.public-questions-title {
  margin: 0;
}

ul.nav-ul {
  display: flex;
  justify-content: flex-end;
}

h4.modal-title {
  color: black;
}

.no-padding {
  padding: 0;
}

div.titles-row {
  height: 710px;
  font-family: 'Patua One', cursive;
  background-image: url('/assets/header4.jpg');
  background-size: cover;
  background-repeat: no-repeat;
}

.title-link {
  height: 100%;
}

p.post-title {
  font-size: 1.32em;
  font-weight: 600;
  color: #3A96AF;
}

.answer-input {
  border: 1px solid #ddd !important;
  border-radius: 10px !important;
  margin: 0 !important;
  height: 35px !important;
}

.form-answer-container {
  margin-top: 20px !important;
}

.answer-button {
  border-radius: 20px;
}

.answers-container {
  margin-left: 0 !important;
  width: 87% !important;
}

.answer-row {
  background-color: #ddd;
  margin-left: 8px !important;
  border-radius: 8px;
}

.user-box-title {
   background-color: #ddd;
} 

.user-box-title > div > span {
  font-size: 18px;
}

.users-collection {
  margin-top: 0 !important;
}

.answer-textarea {
  resize: none;
  height: 80px;
  border: 1px solid #ddd;
  border-radius: 8px;
}

.answers-title-row {
  border-bottom: 1px solid #ddd;
}

.answers-row {
  background-color: #ddd;
  border-radius: 12px;
  margin: 10px !important;
  padding-bottom: 10px !important;
}

.online-text {
  color: #4BB543
}

.send-icon {
  color: black;
}

.friends-ul {
  padding: 30px !important;
}

.friends-card {
  height: 50px;
  background-color: #ddd !important;
  border-radius: 5px;
  display: flex;
  align-items: center;
}

.friends-img {
  width: 25px;
}

.tabs a {
  color: black !important;
}

.tabs a:focus {
  background-color: rgba(58, 150, 175, 0.2) !important;
}

.indicator {
  background-color: #3A96AF !important;
}

.activity-results-div {
  border-bottom: 1px solid #ddd;
  padding: 10px 0;
}

.search-question-title {
  font-size: 20px !important;
  color: #3A96AF !important;
}

.question-content {
  color: black !important;
}

.question-result-card {
  border-bottom: 1px solid #ddd !important;
}

.results-title {
  margin-left: 10px;
}

.users-panel {
  padding: 0 !important;
  border: 1px solid #ddd;
  border-radius: 10px;
}

.btn-github {
  background-color: #24292e !important;
  margin: 0 !important;
}

.auth-form-icon {
  color: black;
}

.auth-form-row {
  border: 3px solid #ddd;
  border-radius: 10px;
}

.submit-btn-container {
  padding: 0 !important;
}

.submit-btn-container > button {
  width: 100%;
}

.submit-btn-container > a {
  width: 100%;
}

.sidenav-row{
  display: flex;
  align-items: center;
}

.post-info {
  margin: 0; 
}

.post-info-date {
  font-size: 14px;
  font-style: italic;
}

.modal-ask-title {
  text-align: center;
  font-size: 25px;
  margin-bottom: 0;
}

.ask-textarea {
  border: 3px solid #ddd;
  border-radius: 10px;
  height: 200px;
  resize: none;
}

.edit-question-row {
  border: 3px solid #ddd !important;
  border-radius: 10px !important;
  padding-left: 10px !important;
}

.edit-info-container {
  padding: 0 !important;
  margin-left: 10px !important;
}  

.edit-info-row {
  border: 3px solid #ddd !important;
  border-radius: 10px !important;
  resize: none;
}
